
import ajax from '@/utils/request.js'

/**
  * 
  *
  *    
  * collect       true  false 
  * page       
  * per_page   
  */
export const getImages = (paramObj) => {
  return ajax({
    method: 'GET',
    url: '/mp/v1_0/user/images',
    params: paramObj
  })
}
/**
 * 
 * @param {number} id 
 */
export const delImage = id => {
  return ajax({
    method: 'DELETE',
    url: '/mp/v1_0/user/images/' + id
  })
}
/**
 * 
 */
export const modImage = (id, data) => {
  return ajax({
    method: 'PUT',
    url: '/mp/v1_0/user/images/' + id,
    data
  })
}
