<template>
  <div class="my-cover">
    <!--  -->
    <div class="btn_img" @click="openDialog()">
      <!-- v-model -->
      <img :src="value || coverImageUrl" />
    </div>
    <!--  -->
    <el-dialog
      :append-to-body="true"
      :visible.sync="dialogVisible"
      width="720px">
      <!-- tab -->
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="image">
            <div style="padding-bottom: 20px;">
              <el-radio-group v-model="collect"  @change="hCollectChange" size="mini">
                  <el-radio-button :label="false"></el-radio-button>
                  <el-radio-button :label="true"></el-radio-button>
              </el-radio-group>
            </div>
            <!--  -->
            <el-row :gutter="10" class="img_list">

              <!-- imageurlselectedImageUrlselected
                :class="{}"
                true

              -->
                <el-col
                  v-for="image in images"
                  :key="image.id"
                  class="img_item"
                  :class="{selected: image.url===selectedImageUrl}"
                  :xs="12" :sm="6" :md="6" :lg="4"
                >
                  <el-image
                    @click.native="hImageClick(image)"
                    style="height: 100px;"
                    :src="image.url"
                    fit="cover"></el-image>
                </el-col>
            </el-row>
            <!-- / -->

            <!-- 
              totalpage-size

              1. total
              10
              2. current-change,
            -->
            <el-pagination
              style="margin-top:10px;"
              :hide-on-single-page="true"
              background
              @current-change="hPageChange"
              layout="prev, pager, next"
              :page-size="per_page"
              :total="total_count">
            </el-pagination>
        </el-tab-pane>

        <el-tab-pane label="" name="upload">

          <!--
            ajax

            action
            show-file-list:false
            on-success 
            before-upload: 
            name: 
          -->
          <el-upload
            class="avatar-uploader"
            action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
            :headers="headers"
            :on-success="hUploadSuccess"
            name="image"
            :show-file-list="false"
            :before-upload="beforeAvatarUpload">
            <!--
              
              
              onload
              - src
              onload
            -->
            <el-image v-if="imgSrc" :src="imgSrc" class="avatar"></el-image>
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="hConfirmImage"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import defaultImg from '@/assets/default.png'
import { getUser } from '@/utils/storage.js'
import { getImages } from '@/api/image.js'
export default {
  name: 'MyCover',
  // value v-model
  props: ['value'],
  data () {
    return {
      // 
      // coverImageUrl: '../assets/default.png',
      coverImageUrl: defaultImg,

      // 
      imgSrc: null,

      // axios,token
      // token
      headers: {
        Authorization: `Bearer ${getUser().token}`
      },

      // 
      // 
      selectedImageUrl: null,
      // 
      dialogVisible: false,
      // tabsname
      activeName: 'image',

      images: [], // 
      collect: false, //   truefalse
      cur_page: 1, // 
      per_page: 10, // 10

      total_count: 0 // 
    }
  },
  methods: {
    openDialog () {
      // 
      this.activeName = 'image'
      this.imgSrc = null
      this.selectedImageUrl = ''

      // 
      this.dialogVisible = true

      // 
      this.loadImages()
    },
    async loadImages () {
      try {
        const res = await getImages({
          collect: this.collect, // 
          per_page: this.per_page, // 
          page: this.cur_page // 
        })
        // 
        this.images = res.data.data.results

        // 
        this.total_count = res.data.data.total_count
      } catch (err) {
        console.log(err)
      }
    },
    // 
    hPageChange (curPage) {
      // alert(curPage)
      // 1. 
      this.cur_page = curPage
      // 2. 
      this.loadImages()
    },
    // & 
    hCollectChange () {
      // console.log(this.collect)
      // 
      this.loadImages()
    },
    hImageClick (image) {
      this.selectedImageUrl = image.url
      console.log(image, this.selectedImageUrl)
    },
    // 
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M
    },
    // res
    hUploadSuccess (res) {
      console.log(res)
      // 
      // 
      this.imgSrc = res.data.url
      this.$message.success('')
    },
    hConfirmImage () {
      console.log(this.activeName)
      // 1 tab
      if (this.activeName === 'image') {
        // 
        if (!this.selectedImageUrl) {
          this.$message.warning('')
          return
        } else {
          // 2   
          this.coverImageUrl = this.selectedImageUrl

          // inputv-model
          this.$emit('input', this.selectedImageUrl)
        }
      } else if (this.activeName === 'upload') {
        //  
        if (!this.imgSrc) {
          this.$message.warning('')
          return
        } else {
          // 2   
          this.coverImageUrl = this.imgSrc
          // inputv-model
          this.$emit('input', this.coverImageUrl)
        }
      }
      // 3.
      this.dialogVisible = false
    }
  }
}
</script>

<style scoped lang='less'>
//  
.my-cover /deep/ .el-dialog__body {
    padding-bottom: 0px;
    padding-top: 0;
}
// 
.btn_img {
  width: 150px;
  height: 150px;
  border: 1px dashed #ddd;
  img {
    width: 100%;
    height: 100%;
    display: block;
  }
}

// 
.img_list{
  .img_item{
    // 
    cursor: pointer;
    position: relative;
  }
  .selected::after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3) url(../assets/selected.png) no-repeat center / 50px 50px;
  }
}

// 
.avatar{
    width: 178px;
    height: 178px;
    line-height: 178px;
  }
  .avatar-uploader {
      text-align: center;
  }
  .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
  }
  .avatar-uploader /deep/ .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
  }
</style>
